body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", "\5FAE\8F6F\96C5\9ED1" sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.slider {
  width: 100%;
  height: 100%;
  background: #000c17;
}

.swiper_main {
  overflow: visible;
}
.btn {
  width: 0.06rem;
  height: 0.06rem;
  background: #a1a1a3;
  margin-right: 0.12rem;
  border-radius: 50%;
}
.btn_active {
  background: #fff;
}
.pagination_btns {
  display: flex;
  justify-content: center;
}

.home_page {
  width: 100%;
  min-height: 100%;
  position: relative;
  z-index: 100;
}
.home_page .bg1 {
  width: 100%;
}
.home_page .link_to_more {
  width: 1.6rem;
  height: 0.35rem;
  position: absolute;
  top: 20.875rem;
  left: 1.65rem;
  box-sizing: border-box;
}
.home_page .swiper_cont {
  width: 100%;
  position: absolute;
  top: 10.94rem;
  left: 0rem;
  box-sizing: border-box;
}
.home_page .swiper_cont .detail_pic {
  width: 100% !important;
  height: 2.53rem;
}
.home_page .swiper_cont .detail_pic .detail_img {
  width: 100%;
  height: 2.53rem;
  float: left;
}
.home_page .swiper_cont .detail_pic .detail_content {
  height: 0.385rem;
  line-height: 0.385rem;
  text-align: center;
  color: #002d51;
  font-size: 0.085rem;
  letter-spacing: 0.075rem;
}
.home_page .fill_lists_tow {
  display: flex;
}
.home_page .fill_lists_tow .fill_list_tow {
  display: flex;
  height: 0.26rem;
  line-height: 0.26rem;
  margin-bottom: 0.135rem;
}
.home_page .fill_lists_tow .fill_list_tow .fill_list_tow_name {
  font-size: 0.1rem;
  color: #343436;
  width: 0.525rem;
  padding-left: 0.065rem;
  border: 1px solid #545454;
  border-right: none;
  box-sizing: border-box;
  background: #fff;
  position: relative;
}
.home_page .fill_lists_tow .fill_list_tow .fill_list_tow_name::after {
  position: absolute;
  content: "";
  right: 0;
  top: 0.025rem;
  width: 1px;
  height: 0.2rem;
  background: #545454;
}
.home_page .fill_lists_tow .fill_list_tow .fill_list_tow_val {
  width: 0.8rem;
  height: 100%;
  box-sizing: border-box;
  border: 1px solid #545454;
  border-left: none;
  background: #fff;
}
.home_page .fill_lists_tow .fill_list_tow .fill_list_tow_val select {
  width: 100%;
  height: 100%;
  float: left;
  border: 0;
  outline: none;
  padding-left: 0.05rem;
  box-sizing: border-box;
  font-size: 0.1rem;
  -webkit-appearance: none;
}
.home_page .fill_lists_tow .fill_list_three .fill_list_tow_name {
  width: 0.32rem;
  border-left: none;
}
.home_page .fill_lists_tow .fill_list_three .fill_list_tow_name::after {
  display: none;
}
.home_page .fill_lists_tow .fill_list_three .fill_list_tow_val {
  width: 1.09rem;
  position: relative;
}
.home_page .fill_lists_tow .fill_list_three .fill_list_tow_val .icon {
  position: absolute;
  top: 0;
  width: 0.13rem;
  height: 0.13rem;
  right: 0.05rem;
  top: 0.06rem;
}
.home_page .fill_lists_tow .fill_list_three .fill_list_tow_val select {
  width: 0.835rem;
}
.home_page .peizhi {
  width: 100%;
  height: 5rem;
  overflow-y: auto;
  position: absolute;
  left: 0;
  top: 15.6rem;
}
.home_page .peizhi img {
  width: 100%;
  float: left;
}
.home_page .vadio {
  height: 2.255rem;
  width: 100%;
  position: absolute;
  top: 7.8rem;
  left: 0rem;
  box-sizing: border-box;
}
.home_page .vadio video {
  width: 100%;
  height: 100%;
  object-fit: contain;
}
.home_page .ask_price {
  width: 3.18rem;
  height: 1.64rem;
  position: absolute;
  top: 5.22rem;
  left: 0.295rem;
  box-sizing: border-box;
}
.home_page .ask_price .fill_lists .fill_list {
  display: flex;
  margin-bottom: 0.08rem;
  height: 0.355rem;
  line-height: 0.355rem;
  background: #eeeeee;
}
.home_page .ask_price .fill_lists .fill_list .fill_val {
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  border-left: none;
  background: #eeeeee;
}
.home_page .ask_price .fill_lists .fill_list .fill_val select {
  width: 100%;
  height: 100%;
  float: left;
  border: 0;
  outline: none;
  -webkit-appearance: none;
  background: #eeeeee;
  color: #101117;
  padding-left: 0.2rem;
  box-sizing: border-box;
  font-size: 0.19rem;
  border: 0.005rem solid #2854d9;
}
.home_page .ask_price .fill_lists .fill_list .fill_val input {
  width: 100%;
  height: 100%;
  float: left;
  border: 0;
  outline: none;
  padding-left: 0.2rem;
  box-sizing: border-box;
  font-size: 0.19rem;
  background: #eeeeee;
  color: #101117;
  border: 0.005rem solid #2854d9;
}
.home_page .ask_price .fill_lists .fill_list .fill_val input::-webkit-input-placeholder {
  color: #101117;
}
.home_page .ask_price .fill_lists .fill_list_last {
  margin-bottom: 0;
}
.home_page .ask_price .is_read {
  height: 0.32rem;
  display: flex;
  align-items: center;
  font-size: 0.095rem;
  color: #343434;
}
.home_page .ask_price .is_read .is_read_cont {
  padding-left: 0.09rem;
}
.home_page .ask_price .submit_btn {
  width: 3.18rem;
  height: 0.355rem;
  line-height: 0.355rem;
  background: #2f3438;
  font-size: 0.19rem;
  color: #fff;
  text-align: center;
  font-weight: bold;
}
.home_page .distributor {
  position: absolute;
  width: 100%;
  background: rgba(255, 255, 255, 0.8);
  left: 0;
  top: 8.305rem;
  padding: 0 0.15rem;
  padding-top: 0.18rem;
}
.home_page .distributor .choic_area {
  display: flex;
  justify-content: center;
}
.home_page .distributor .distributor_lists {
  padding-top: 0.18rem;
  height: 2.025rem;
  overflow: auto;
  -webkit-overflow-scrolling: touch;
}
.home_page .distributor .distributor_lists .distributor_list {
  display: flex;
  height: 0.33rem;
  line-height: 0.33rem;
  font-size: 0.1rem;
  color: #333333;
  justify-content: space-between;
  border-top: 1px solid #c2c3c5;
}
.home_page .distributor .distributor_lists .distributor_list .distributor_name {
  text-align: left;
}
.home_page .distributor .distributor_lists .distributor_list .distributor_tel {
  text-align: right;
}
.home_page .distributor .distributor_lists .distributor_list:last-of-type {
  border-bottom: 1px solid #c2c3c5;
}
.home_page .distributor .distributor_tishi {
  height: 0.36rem;
  font-size: 0.1rem;
  color: #a4a4a6;
  text-align: center;
  line-height: 0.36rem;
}
.home_page .car_swiper_one {
  position: absolute;
  left: 0;
  width: 100%;
  top: 20.56rem;
  box-sizing: border-box;
}
.home_page .car_swiper_one .nav_lists {
  display: flex;
  margin: 0 0.145rem;
  margin-bottom: 0.065rem;
}
.home_page .car_swiper_one .nav_lists .nav_list {
  width: 0.85rem;
  height: 0.375rem;
  font-size: 0.16rem;
  background: #fff;
  border: 1px solid #143b80;
  text-align: center;
  font-weight: bold;
  line-height: 0.375rem;
  cursor: pointer;
  color: #143b80;
}
.home_page .car_swiper_one .nav_lists .nav_list_active {
  background: #143b80;
  color: #fff;
}
.home_page .car_swiper_one .car_pic {
  width: 3.75rem;
  height: 2.3rem;
  border-bottom: 1px solid #153a80;
  margin-bottom: 0.07rem;
  position: relative;
  display: flex;
  justify-content: center;
}
.home_page .car_swiper_one .car_pic .car_img {
  width: 3.455rem;
  height: 2.24rem;
}
.home_page .car_swiper_one .car_pic .car_sing {
  position: absolute;
  left: 0.09rem;
  bottom: 0.165rem;
  width: 0.645rem;
  height: 0.28rem;
}
.home_page .details {
  position: absolute;
  top: 25.145rem;
  width: 100%;
  padding: 0 0.15rem;
  box-sizing: border-box;
  left: 0;
}
.home_page .details .detail {
  margin-bottom: 0.27rem;
  position: relative;
}
.home_page .details .detail .detail_sing {
  position: absolute;
  top: 0;
  width: 0.775rem;
  height: 0.185rem;
}
.home_page .details .detail .detail_sing img {
  width: 100%;
  height: 100%;
  float: left;
}
.home_page .details .detail .detail_pic {
  width: 3.455rem;
  height: 2.425rem;
  margin-top: 0.255rem;
  border-bottom: 1px solid #31518e;
  margin-bottom: 0.085rem;
}
.home_page .details .detail .detail_pic .detail_img {
  width: 100%;
  height: 2.2rem;
  float: left;
}
.home_page .details .detail .detail_pic .detail_content {
  height: 0.215rem;
  line-height: 0.215rem;
  text-align: center;
  color: #bc0f2d;
  font-size: 0.085rem;
}
.home_page .details .detail_one .detail_sing {
  left: 0;
}
.home_page .details .detail_two .detail_sing {
  right: 0;
}

.App {
  width: 100vw;
  height: 100vh;
  font-family: "\5FAE\8F6F\96C5\9ED1", Arial;
}

